<template>
  <div>
    <h1>管理员列表</h1>
    <template>
      <el-table :data="items" style="width: 100%">
        <!-- id -->
        <el-table-column label="ID" width="300" prop="_id">
        </el-table-column>
        <!-- 分类名称 -->
        <el-table-column label="用户名" width="300" prop="username">
        </el-table-column>
        <!-- 操作 -->
        <el-table-column label="操作" flxed="right">
          <template slot-scope="scope">
            <!-- 编辑 -->
            <el-button size="mini" @click="$router.push(`/admin_users/edit/${scope.row._id}`)">编辑</el-button>
            <!-- 删除 -->
            <el-button size="mini" type="danger" @click="remove(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </template>
  </div>
</template>

<script>
export default {
  data() {
    return {
      items: []
      // tableData: [{
      //   date: '2016-05-02',
      //   name: '王小虎',
      //   address: '上海市普陀区金沙江路 1518 弄'
      // }]
    }

  },
  methods: {
    async fetch() {
      // 获取数据
      const res = await this.$http.get('rest/admin_users')
      //让后台的数据等于items
      this.items = res.data
    },
    async remove(row) {
      this.$confirm(`是否确定要删除管理员"${row.username}"`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          // 获取相关_id的行删除
          const res = await this.$http.delete(`rest/admin_users/${row._id}`) // eslint-disable-line no-unused-vars
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          this.fetch()
        })
    },
  },

  // created钩子函数在实例创建完成后被立即调用
  created() {
    this.fetch()
  }
}
</script>
