<template>
  <div>

    <div class="login-container">
      <h3 style="text-align:center">代码改变世界</h3>
      <!-- native 监听原生表单时间 prevent 阻止默认提交事件 -->
      <el-form @submit.native.prevent="login">
        <el-form-item label="用户名">
          <el-input v-model="model.username"></el-input>
        </el-form-item>
        <el-form-item label="密码">
          <el-input type="password" v-model="model.password"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" native-type="submit">登陆</el-button>
        </el-form-item>
      </el-form>

    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      model: {}
    }
  },
  methods: {
    async login() {
      const res = await this.$http.post('login', this.model) // eslint-disable-line no-unused-vars
      // console.log(res.data);
      localStorage.token = res.data.token
      this.$router.push('/')
      this.$message({
        type: 'success',
        message: '登陆成功'
      })
      //登陆成功
      console.log(123)
    }
  },


}
</script>

<style>
.login-container {
  width: 300px;
  margin: 200px auto;
}
</style>