import Vue from 'vue'
import VueRouter from 'vue-router'
import Main from '../views/Main.vue'


import ClassifyAdd from '../views/ClassifyAdd.vue'
import ClassifyList from '../views/ClassifyList.vue'


import ArticleAdd from '../views/ArticleAdd.vue'
import ArticleList from '../views/ArticleList.vue'

import AdminUserAdd from '../views/AdminUserAdd.vue'
import AdminUserList from '../views/AdminUserList.vue'

import Login from '../views/Login.vue'

Vue.use(VueRouter)

const routes = [
  { path: '/login', name: 'login', component: Login, meta: { isPublic: true } },
  {
    path: '/',
    name: 'Main',
    component: Main,
    children: [
      { path: '/classifys/add', component: ClassifyAdd },
      // 路由传参一个id
      { path: '/classifys/edit/:id', component: ClassifyAdd, props: true },
      { path: '/classifys/list', component: ClassifyList },


      { path: '/articles/edit/:id', component: ArticleAdd, props: true },
      { path: '/articles/add', component: ArticleAdd },
      { path: '/articles/list', component: ArticleList },


      { path: '/admin_users/edit/:id', component: AdminUserAdd, props: true },
      { path: '/admin_users/add', component: AdminUserAdd },
      { path: '/admin_users/list', component: AdminUserList },
    ]
  },

]

const router = new VueRouter({
  routes
})
router.beforeEach((to, from, next) => {
  if (!to.meta.isPublic && !localStorage.token) {
    return next('/login')
  }
  next()
})

export default router
