<template>
  <div class="about">
    <h1>{{id ? '编辑':'新建'}}文章</h1><br>
    <el-form label-width="120px" @submit.native.prevent="save" style="width:30%">

      <el-form-item label="标题">
        <!-- 标题输入框 -->
        <el-input v-model="model.title"></el-input>
      </el-form-item>
      <el-form-item label="类型">
        <!-- 上级分类下拉菜单 -->
        <el-select v-model="model.classifies" multiple>
          <!-- key优化 label显示的值 value点击所带的值 -->
          <el-option v-for="item in classifies" :key="item._id" :label="item.name" :value="item._id"></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="内容" style="width:300%;">
        <!-- 内容 -->
        <vue-editor v-model="model.content" useCustomImageHandler @image-added="handleImageAdded"></vue-editor>
      </el-form-item>
      <el-form-item label="上传封面">
        <el-upload class="avatar-uploader" :headers="getAuthHeaders()" :action="uploadURL" :show-file-list="false" :on-success="afterUpload">
          <img v-if="model.icon" :src="model.icon" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item>
        <!-- 按钮 -->
        <el-button type="primary" native-type="submit">{{id ? '立即修改' : '立即创建'}}</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { VueEditor } from "vue2-editor";
export default {
  // 接受id
  props: {
    id: {
    }
  },
  components: {
    VueEditor
  },
  data() {
    return {
      model: {
      },
      classifies: [],
    }
  },
  methods: {
    afterUpload(res) {
      this.$set(this.model, 'icon', res.url)
      // this.model.icon = res.url
    },
    async handleImageAdded(file, Editor, cursorLocation, resetUploader) {

      const formData = new FormData();
      formData.append("file", file);
      const res = await this.$http.post("upload", formData)
      Editor.insertEmbed(cursorLocation, "image", res.data.url);
      resetUploader();
    },
    async save() {
      // 发起接口请求  提交到classify 传递的参数 this.name
      let res // eslint-disable-line no-unused-vars
      //新建和修改分开处理
      if (this.id) {
        // 修改 用put请求
        res = await this.$http.put(`rest/articles/${this.id}`, this.model) // eslint-disable-line no-unused-vars
      } else {
        // 新建  用post请求
        res = await this.$http.post('rest/articles', this.model) // eslint-disable-line no-unused-vars
      }
      //跳转
      console.log(this.model);
      this.$router.push("/articles/list")
      //提醒
      this.$message({
        type: 'success',
        message: "保存成功了"
      })
    },
    async fetch() {
      // 发起接口请求  修改
      const res = await this.$http.get(`rest/articles/${this.id}`)
      // 获取数据
      this.model = res.data
    },
    async fetchClassifys() {
      // 发起接口请求  
      const res = await this.$http.get('rest/classifys')
      // 获取数据
      this.classifies = res.data
    },


  },
  // created钩子函数在实例创建完成后被立即调用
  created() {
    this.fetchClassifys()
    this.id && this.fetch()
  }
}
</script>


<style>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>