<template>
  <div class="about">
    <h1>{{id ? '编辑':'新建'}}管理员账号</h1><br>
    <el-form label-width="120px" @submit.native.prevent="save" style="width:30%">
      <el-form-item label="用户名">
        <!-- 分类名称输入框 -->
        <el-input v-model="model.username"></el-input>
      </el-form-item>
      <el-form-item label="密码">
        <!-- 分类名称输入框 -->
        <el-input v-model="model.password"></el-input>
      </el-form-item>
      <el-form-item>
        <!-- 按钮 -->
        <el-button type="primary" native-type="submit">{{id ? '立即修改' : '立即创建'}}</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  // 接受id
  props: {
    id: {
    }
  },
  data() {
    return {
      model: {

      },
    }
  },
  methods: {

    async save() {
      // 发起接口请求  提交到classify 传递的参数 this.name
      let res // eslint-disable-line no-unused-vars
      //新建和修改分开处理
      if (this.id) {
        // 修改 用put请求
        res = await this.$http.put(`rest/admin_users/${this.id}`, this.model) // eslint-disable-line no-unused-vars
      } else {
        // 新建  用post请求
        res = await this.$http.post('rest/admin_users', this.model) // eslint-disable-line no-unused-vars
      }
      //跳转
      console.log(this.model);
      this.$router.push("/admin_users/list")
      //提醒
      this.$message({
        type: 'success',
        message: "保存成功了"
      })
    },
    async fetch() {
      // 发起接口请求  修改
      const res = await this.$http.get(`rest/admin_users/${this.id}`)
      // 获取数据
      this.model = res.data
    },
  },
  // created钩子函数在实例创建完成后被立即调用
  created() {
    this.id && this.fetch()
  }
}
</script>

